
.backdrop {
    position: fixed;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    overflow-y: scroll; }


    .modal {
    position: absolute;
    height: auto;
    top: 75%;
    left: 50%;
    width: 320px;
    transform: translate(-50%, -50%) scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
    background-color: #ffffff;
    padding: 40px 40px 90px 40px;
    visibility: visible;
    background-color: var(--main-text-color);
    overflow: auto;
    background-color: #FFF5F6;
    background-image: url(/images/about/modal-bg-img.svg);
    background-repeat: no-repeat;
    background-position: 50% 97%;
    border-radius: 5px;
@media screen and (min-width: 768px) {
    width: 662px;
    padding: 60px 60px 90px 40px;
    top: 50%;
}
}

.list {
    text-decoration: none;
    list-style: none;
}

.modal__text {
    width: 220px; 
    text-align: left;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 162%;
    color: var(--third-text-color);
        &:not(:last-child) {
    margin-bottom: 1.5em;
}
@media screen and (min-width: 768px) {
    width: 463px;
}
}

.modal__text--black {
margin-bottom: 2em;
color: var(--second-text-color);
}

.modal__modal-button {
position: absolute;
}

.modal-button {
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
    padding: 0;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    background: var(--accent-bg-second-button);
    background-image: url(/images/about/svg-close.svg);
    background-repeat: no-repeat;
    background-position: center;
    z-index: 1; 
    transition: transfor 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:focus {
    transform: scale(0.9);
}
} 

.backdrop.is-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
}

.backdrop.is-hidden .modal {
    transform: translate(-50%, -50%) scale(1.1);
    visibility: hidden;
} 